// 
let asyncRoutes = [

]

import { setupLayouts } from 'virtual:generated-layouts'
import generatedRoutes from 'virtual:generated-pages'

if (useSettingsStore(pinia).app.routeBaseOn === 'filesystem') {
    constantRoutes = generatedRoutes.filter(item => {
        return item.meta?.enabled !== false && item.meta?.constant === true
    })
    asyncRoutes = setupLayouts(generatedRoutes.filter(item => {
        return item.meta?.enabled !== false && item.meta?.constant !== true && item.meta?.layout !== false
    }))
}



const convertNameAndPath = (routes) => {
    routes.forEach(item => {
        if (item.name) {
            item.name = item.name.replace(/(^\d+-)|-\d+/g, "")
        }
        if (item.path) {
            item.path = item.path.replace(/(^\/\d+-)|\/\d+-/g, "/")
        }

        if (item.children) {
            convertNameAndPath(item.children)
        }
    })
}

// convertNameAndPath(constantRoutes)
// convertNameAndPath(systemRoutes)
convertNameAndPath(asyncRoutes)

export {
    constantRoutes,
    systemRoutes,
    asyncRoutes
}
